import React from 'react';
import { render } from 'react-dom';
import DividensChart from './Dividends';
import HistoricalPrice from './HistoricalPrice';
import MarketAnalysis from './MarketAnalysis';
import { HashRouter as Router, Switch, Route, } from "react-router-dom";
import Dashboard from './template/Dashboard';
render(
  <React.Fragment>
    <Dashboard>
      <Router >
        <Switch>
          <Route exact path="/dividends/:market/:id" component={DividensChart} />
          <Route exact path="/price/:market/:id" component={HistoricalPrice} />
          <Route exact path="/market/:market" component={MarketAnalysis} />
        </Switch>
      </Router>
    </Dashboard>
  </React.Fragment>,
  document.getElementById('root'),
);