import React, { useState, useEffect } from 'react';
import { useTheme } from '@material-ui/core/styles';
import { DividendsEntity, Info } from "./models";
import { LineChart, Line, XAxis, YAxis, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { get_dividends, get_info } from './utils';
import { useParams } from 'react-router-dom';
import Title from './template/Title';

export default function DividensChart() {
  const [data, setData] = useState<{ x: string, y: number }[]>([]);
  const [currency, setCurrency] = useState<string>("$");
  const params: { market: string, id: string } = useParams()
  const theme = useTheme()

  useEffect(() => {
    get_dividends(params.market, params.id).then(response => {
      let dividends = response as DividendsEntity[]
      var data = []
      // convert date
      for (let item of dividends) {
        let d = new Date(item.Date)
        data.push({
          x: d.toLocaleDateString(),
          y: item.Dividends,
        })
      }
      setData(data)
    })
    get_info(params.market, params.id).then(response => {
      let info = response as Info
      setCurrency(info.currency)
    })
  }, [params.id, params.market])


  return (
    <React.Fragment>
      <Title>{params.id} dividends historical</Title>
      <ResponsiveContainer>
        <LineChart
          width={500}
          height={300}
          data={data}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5
          }}
        >
          <XAxis dataKey="x" stroke={theme.palette.text.secondary}/>
          <YAxis tickFormatter={d => `${currency} ${d}`} stroke={theme.palette.text.secondary}/>
          <Tooltip />
          <Legend />
          <Line
            type="monotone"
            dataKey="y"
            name="Dividends"
            stroke={theme.palette.primary.main}
            dot={false}
          />

        </LineChart>
      </ResponsiveContainer>
    </React.Fragment>)
}